/* Root variables */
:root {
  --green: #13553f;
  --light-green: #46a585;
  --light-grey: #dddddd;
  --white: #ffffff;
  --text-color-green: #13553f;
  --text-color-black: #2c2c2c;
  --text-color-white: #ffffff;
}

/* General styling */
html {
  color: var(--text-color-dark);
  height: 100%;
  width: 100%;
  font-weight: 200;
}

body {
  margin: 0;
  padding: 70px 20px;
}

h1 {
  font-size: 2.5em;
  font-weight: 300;
  font-family: Bebas Neue;
}

h2 {
  font-family: Bebas Neue;
  font-size: 2em;
}

h3 {
  font-family: Roboto;
}

p {
  font-family: Roboto;
  font-weight: 300;
}

a {
  font-family: Roboto;
  font-weight: 400;
}
/* Topbar styling */
.topbar {
  background-color: var(--white);
  color: var(--text-color-light);
  padding: 20px;
  height: 30px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  box-shadow: 0px 1px 10px var(--light-grey);
}

.topbar h2 {
  font-weight: 300;
  margin: auto;
}

.back_btn {
}

/* Navigation styling */
.tabbar {
  background-color: var(--white);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  min-width: 100%;
  box-shadow: 0px 1px 10px var(--light-grey);
  display: flex;
}

.nav_left {
  width: 30%;
  float: left;
  display: flex;
}

.nav_middle {
  width: 35%;
}

.nav_right {
  width: 30%;
  float: right;
  display: flex;
}

/* Style the links inside the navigation bar */
.nav_item {
  color: var(--text-color-light);
}

/* Add a color to the active/current link */
.tabbar a.active {
}

.nav_img_raised {
  border-radius: 50%;
  padding: 20px 20px;
  box-shadow: 0px 1px 10px var(--light-grey);
  position: absolute;
  bottom: 30%;
  left: 50%;
  transform: translate(-50%, 0%);
  z-index: -1;
  background-color: var(--white);
}

.nav_img {
  width: 25px;
  padding: 20px 20px;
  color: var(--green);
}

/* Page styling */
.page {
  display: none;
  /*min heigh is screen heigh minus height of tab and top bar */
  min-height: calc(100vh - 110px);
  background: var(--white);
  animation: fadeIn 0.4s;
  /* padding equals height of tabbar and topbar */
}

.overlay {
  position: relative;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  background-color: var(--green);
  opacity: 0.7;
  z-index: 13;
}

.half-overlay {
  position: relative;
  width: 100%;
  height: 45vh;
  object-fit: cover;
  background-color: var(--green);
  opacity: 0.7;
  z-index: 13;
}

/* Button styling */
.btn {
  width: 300px;
  border: 1.5px solid var(--white);
  border-radius: 5px;
  background: transparent;
  font-size: 1.5em;
  font-family: Bebas Neue;
  color: var(--text-color-white);
  margin: 10px 0;
  padding: 10px;
}
